import { FormControl } from '@angular/forms';
import {
    Component,
    ChangeDetectionStrategy,
    ViewChild,
    ElementRef,
    Renderer2,
    AfterViewInit,
} from '@angular/core';

export interface UserProjectInterface {
    name: string;
    imagine: string;
    source: string;
}

export interface UserInterface {
    avatar: string;
    nickname: string;
    position: string;
    projects: UserProjectInterface[];
    qoute?: string;
}

interface ItemInterface {
    caption: string;
    elements: { name: string; value: string; canEdit: boolean }[];
}

