import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, tap, retry } from 'rxjs/operators';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

export interface UserInterface {
    Message: string;
    Result: boolean;
}

@Injectable({
    providedIn: 'root'
})
export class AuthService {

    public auth$: BehaviorSubject<boolean> = new BehaviorSubject(false);

    public currentUser: UserInterface;

    public sessionStorage: Storage = window.sessionStorage;

    private httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    get authorization(): boolean {
        const auth = this.sessionStorage.getItem('auth');

        if (auth) {
            if (auth === 'true') {
                return true;
            }

            return false;
        }

    }

    /**
     *
     * @param login получает логин пользователя для регистрации на сервере
     * @param password строка, по которой будет определяться пароль пользователя
     * @param email эл.почта пользователя, по которой связывается регистрация на серере
     */
    public register$(login: string, password: string, email: string): Observable<UserInterface | null | string> {
        return this.httpClient.post<UserInterface>('https://api.microworld.exluap.com/api/user/register', { login, password, email }, this.httpOptions).pipe(
            catchError(err => {
                if (err.error) {
                    return of(err.error)
                }

                return of(null);
            })
        );
    }

    /**
     *
     * @param login
     * @param password
     */
    public login$(login: string, password: string): Observable<UserInterface | null> {
        console.log({ login, password })

        return this.httpClient.post<UserInterface>('https://api.microworld.exluap.com/api/user/login', { login, password }, this.httpOptions).pipe(
            catchError(err => {
                if (err.error) {
                    return of(err.error)
                }

                return of(null);
            })
        );
    }

    /**
     *
     */
    public updateSessionStorage(): void {
        this.sessionStorage.setItem('auth', 'true')
    }


    // public resetPassword$(email: string): Observable<UserInterface | null> {
    //     return this.httpClient.post<UserInterface>('', email, this.httpOptions).pipe(
    //         catchError(() => {
    //             return of(null);
    //         })
    //     );
    // }

    public getUUID() {
        
    }


    public getUserInfo$(UUID: string): Observable<UserInterface> {
        return this.httpClient.get<UserInterface>('')
    }



    constructor(private httpClient: HttpClient) { }
}


