import { Router } from '@angular/router';
import { AuthService } from '../pages/auth/services/auth.service';
import { Component, OnInit, ChangeDetectionStrategy, ViewChild, ElementRef, AfterViewChecked, Renderer2 } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

interface MenuItemInterface {
    caption: string;
    routerLink: string;
    disabled?: boolean;
    visible?: boolean;
    active?: boolean;
}

@Component({
    selector: 'app-main',
    templateUrl: './main.component.html',
    styleUrls: ['./main.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})

export class MainComponent implements OnInit, AfterViewChecked {

    public widgets = [];

    public fastAccessIsVisible = true;

    constructor(
        private readonly authService: AuthService,
        private router: Router,
        private renderer: Renderer2,
    ) { }

    ngOnInit(): void {
        this.widgets = [
            {
                icon: 'iconSearchLarge',
            }, {
                icon: 'iconMailLarge',
            },
        ];


        // if (!this.authService.authorization) {
        //     this.router.navigateByUrl('/auth');
        // }
    }

    ngAfterViewChecked(): void {

    }

    toggleVisibleFastAccess(): void {
        this.fastAccessIsVisible = !this.fastAccessIsVisible
    }
}
