import { Component, OnInit, ChangeDetectionStrategy, TemplateRef, Output, EventEmitter, Input, HostBinding } from '@angular/core';
import { SizeXL, SizeXS } from 'projects/angular-core/src/types/size';
import { BehaviorSubject } from 'rxjs';

export type ButtonColors = 'primary' | 'secondary' | 'error' | 'success' | 'info' | 'warning' | 'custom';


export type ColorSchemeType =
    'custom' |
    'default' |
    'office' |
    'strict' |
    'wild' |
    'gamer'


@Component({
    selector: 'mui-button',
    templateUrl: './button.template.html',
    styleUrls: ['./button.style.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    animations: [],
})
export class MuiButtonComponent implements OnInit {

    @HostBinding('attr.data-host-color')
    @Input()
    color: ButtonColors | string = 'primary';

    @HostBinding('attr.data-host-size')
    @Input()
    size: SizeXS | SizeXL = 'm';

    @HostBinding('attr.data-host-disabled')
    @Input()
    disabled: boolean;

    @Input()
    appearance: string;

    @Input()
    showLoader: boolean;

    @Input()
    colorScheme?: ColorSchemeType;


    /**
     * Флаг, отвечающий за то, 
     * что в приоритете будет использоваться конфигурация пользователя
     */
    @Input()
    isCustom: boolean;


    public style$: BehaviorSubject<object> = new BehaviorSubject<object>({});
    public style: Record<string, string> = {};

    /**
     * Если флаг @isCustom установлен,
     * тогда может принимать в себя конфигурации для настройки стилей кнопки
     */
    @Input()
    set congif(config: Record<string, string>) {
        this.style = this.congif;

        if (this.isCustom) {
            this.style$.next(this.congif);
        }

        console.log(config);
    }

    /**
     * Если флаг @isCustom установлен, 
     * тогда может принимать в себя конфигурации для настройки стилей контента кнопки 
     */
    @Input()
    set contentConfig(conf: Record<string, string>) {

    }

    @Output()
    click: EventEmitter<void> = new EventEmitter();

    constructor() { }

    ngOnInit(): void {
    }
}
