import { SizeL } from '../../../../../projects/angular-core/src/types/size';
import { Component, Output, EventEmitter, ChangeDetectionStrategy, Input, HostBinding } from '@angular/core';

@Component({
    selector: 'app-menu-burger',
    templateUrl: './menu-burger.component.html',
    styleUrls: ['./menu-burger.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MenuBurgerComponent {
    @Input()
    @HostBinding('attr.data-host-size')
    size: SizeL = 'l';

    @Input()
    @HostBinding('class._active')
    active = false;

    @Output()
    eventClick = new EventEmitter<boolean>();

    @Input()
    customStyle?: Record<string, string>;

    public menuClick(): void {
        this.active = !this.active;
        this.eventClick.emit(this.active);
    }
}
